"use client";

import { useEffect, useState, useRef } from "react";
import { useRouter } from "next/navigation";
import SearchBox from "@/components/SearchBox";
import AnimeCard from "@/components/AnimeCard";

export default function Page() {
  const router = useRouter();
  const [items, setItems] = useState<any[]>([]);
  const [page, setPage] = useState(1);
  const [term, setTerm] = useState("");
  const [finished, setFinished] = useState(false);
  const [loading, setLoading] = useState(false);
  const [showBackButton, setShowBackButton] = useState(false);

  const observerRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    const savedTerm = localStorage.getItem("lastAnimeSearch");
    if (savedTerm && savedTerm.trim() !== "") {
      setTerm(savedTerm);
      setShowBackButton(true);
    }
  }, []);

  useEffect(() => {
    loadItems(term, page);
  }, [page, term]);

  async function loadItems(searchTerm: string, currentPage: number) {
    if (loading || finished) return;
    setLoading(true);
    try {
      let url = "";
      
      if (searchTerm && searchTerm.trim() !== "") {
        const encodedTerm = encodeURIComponent(searchTerm.trim());
        url = `https://api.jikan.moe/v4/anime?q=${encodedTerm}&page=${currentPage}`;
      } else {
        url = `https://api.jikan.moe/v4/seasons/now?page=${currentPage}`;
      }

      const res = await fetch(url);
      
      if (!res.ok) {
        throw new Error(`Erro HTTP: ${res.status}`);
      }
      
      const json = await res.json();
      const data = json.data ?? [];

      if (data.length === 0) {
        setFinished(true);
      } else {
        setItems(prev => currentPage === 1 ? data : [...prev, ...data]);
      }

      if (searchTerm && searchTerm.trim() !== "") {
        localStorage.setItem("lastAnimeSearch", searchTerm);
        setShowBackButton(true);
      }
    } catch (e) {
      console.error("Erro na busca:", e);
      setFinished(true);
      setItems([]);
    } finally {
      setLoading(false);
    }
  }

  function handleSearch(newTerm: string) {
    const trimmedTerm = newTerm.trim();
    setTerm(trimmedTerm);
    setPage(1);
    setItems([]);
    setFinished(false);
    
    if (trimmedTerm === "" || trimmedTerm === "." || trimmedTerm === "?") {
      localStorage.removeItem("lastAnimeSearch");
      setShowBackButton(false);
    }
  }

  function handleClearSearch() {
    setTerm("");
    setPage(1);
    setItems([]);
    setFinished(false);
    localStorage.removeItem("lastAnimeSearch");
    setShowBackButton(false);
  }

  useEffect(() => {
    if (!observerRef.current) return;

    const observer = new IntersectionObserver(entries => {
      if (entries[0].isIntersecting && !finished && !loading) {
        setPage(prev => prev + 1);
      }
    });

    observer.observe(observerRef.current);
    return () => observer.disconnect();
  }, [finished, loading]);

  return (
    <div
      style={{
        width: "100vw",
        minHeight: "100vh",
        backgroundImage: "url('/scenario.png')",
        backgroundSize: "cover",
        backgroundPosition: "center",
        padding: 20,
        display: "flex",
        flexDirection: "column",
        alignItems: "center",
      }}
    >
      <h1 style={{ color: "white", marginBottom: 20 }}>Buscar Animes</h1>

      {showBackButton && term && term.trim() !== "" && (
        <div style={{
          width: "100%",
          maxWidth: "1000px",
          background: "rgba(0, 0, 0, 0.8)",
          borderRadius: "10px",
          padding: "15px 20px",
          marginBottom: "20px",
          border: "2px solid #1e88e5",
          display: "flex",
          justifyContent: "space-between",
          alignItems: "center",
          flexWrap: "wrap",
          gap: "10px"
        }}>
          <div style={{ color: "white" }}>
            <div style={{ 
              display: "flex", 
              alignItems: "center", 
              gap: "10px",
              marginBottom: "5px"
            }}>
              <span style={{ fontSize: "20px" }}>üîç</span>
              <span style={{ fontSize: "16px", fontWeight: "bold" }}>
                Pesquisa atual: <span style={{ color: "#90caf9" }}>{term}</span>
              </span>
            </div>
            <div style={{ 
              fontSize: "14px", 
              color: "#aaa",
              display: "flex",
              alignItems: "center",
              gap: "5px"
            }}>
              <span>‚úÖ</span>
              <span>Sua pesquisa ser√° mantida ao voltar de qualquer anime</span>
            </div>
          </div>
          
          <div style={{ display: "flex", gap: "10px" }}>
            <button
              onClick={handleClearSearch}
              style={{
                padding: "8px 16px",
                background: "#ff4444",
                color: "white",
                border: "none",
                borderRadius: "6px",
                cursor: "pointer",
                fontWeight: "bold",
                fontSize: "14px",
                display: "flex",
                alignItems: "center",
                gap: "5px",
                transition: "all 0.3s"
              }}
              onMouseEnter={(e) => {
                e.currentTarget.style.background = "#cc0000";
                e.currentTarget.style.transform = "scale(1.05)";
              }}
              onMouseLeave={(e) => {
                e.currentTarget.style.background = "#ff4444";
                e.currentTarget.style.transform = "scale(1)";
              }}
            >
              <span>üóëÔ∏è</span>
              <span>Limpar</span>
            </button>
            
            <button
              onClick={() => {
                window.scrollTo({ top: 0, behavior: "smooth" });
                setPage(1);
                setItems([]);
                setFinished(false);
                loadItems(term, 1);
              }}
              style={{
                padding: "8px 16px",
                background: "#1e88e5",
                color: "white",
                border: "none",
                borderRadius: "6px",
                cursor: "pointer",
                fontWeight: "bold",
                fontSize: "14px",
                display: "flex",
                alignItems: "center",
                gap: "5px",
                transition: "all 0.3s"
              }}
              onMouseEnter={(e) => {
                e.currentTarget.style.background = "#0d47a1";
                e.currentTarget.style.transform = "scale(1.05)";
              }}
              onMouseLeave={(e) => {
                e.currentTarget.style.background = "#1e88e5";
                e.currentTarget.style.transform = "scale(1)";
              }}
            >
              <span>üîÑ</span>
              <span>Recarregar</span>
            </button>
          </div>
        </div>
      )}

      <SearchBox onSearch={handleSearch} />

      <div
        style={{
          display: "grid",
          gridTemplateColumns: "repeat(auto-fill, minmax(180px, 1fr))",
          gap: 20,
          width: "100%",
          maxWidth: 1000,
        }}
      >
        {items.map((anime) => (
          <div
            key={anime.mal_id}
            onClick={() => router.push(`/${anime.mal_id}`)}
            style={{ cursor: "pointer" }}
          >
            <AnimeCard anime={anime} />
          </div>
        ))}
      </div>

      {loading && <p style={{ color: "white", marginTop: 20 }}>Carregando...</p>}
      {!loading && items.length === 0 && (
        <p style={{ color: "white", marginTop: 20 }}>Nenhum anime encontrado.</p>
      )}

      <div ref={observerRef} style={{ height: 20 }} />
    </div>
  );
}